.cover {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100vh;
    background-color: #1f293c;
    z-index: 99;
    opacity: 1;
    overflow: hidden; }
    
 .logo {
    position: absolute;
    top: 50vh;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 400px;
    height: 240px;
    margin-top: 20px;
    z-index: 100; }
    .logo a {
        display: block;
        width: 100%;
        height: 100%; }

#svg-logo {
    width: 100%;
    height: 100%; }


#mask-letters {
    stroke-dasharray: 3246.820556640625;
    stroke-dashoffset: 3246.820556640625; }
#mask-underline {
    stroke-dasharray: 614.4139404296875;
    stroke-dashoffset: 614.4139404296875; }

.mask-yellow-1 {
    stroke-dasharray: 315.3731994628906;
    stroke-dashoffset: 315.3731994628906; }

.mask-yellow-2 {
    stroke-dasharray: 347.79058837890625;
    stroke-dashoffset: 347.79058837890625; }

.mask-blue-1 {
    stroke-dasharray: 315.3731994628906;
    stroke-dashoffset: 315.3731994628906;
    stroke-width: 26; }

.mask-blue-2 {
    stroke-dasharray: 347.79058837890625;
    stroke-dashoffset: 347.79058837890625; }

.infinite .mask-blue-1{
    -webkit-animation: drawing_blue_1_back 0.6s linear forwards,
    drawing_blue_1_infinite 8s 2s linear infinite;
        -moz-animation: drawing_blue_1_back 0.6s linear forwards,
    drawing_blue_1_infinite 8s 2s linear infinite;
             animation: drawing_blue_1_back 0.6s linear forwards,
    drawing_blue_1_infinite 8s 1.6s linear infinite; }

.infinite .mask-blue-2{
    -webkit-animation: drawing_blue_2_back 0.6s 0.6s linear forwards,
    drawing_blue_2_infinite 8s 6.9s linear infinite;
        -moz-animation: drawing_blue_2_back 0.6s 0.6s linear forwards,
    drawing_blue_2_infinite 8s 6.9s linear infinite;
             animation: drawing_blue_2_back 0.6s 0.6s linear forwards,
    drawing_blue_2_infinite 8s 6.9s linear infinite; }

@keyframes drawing_blue_1_back {
    0% {
      stroke-dashoffset: 0; }
    100% {
      stroke-dashoffset: -315.3731994628906; } }

@keyframes drawing_blue_2_back {
    0% {
        stroke-dashoffset: 0; }
    100% {
        stroke-dashoffset: -347.79058837890625; } }

@keyframes drawing_blue_1_infinite {
    0% {
        stroke-dashoffset: 60;
        stroke-dasharray: 60 315.1544494628906; }
    100% {
        stroke-dasharray: 100 315.1544494628906;
        stroke-dashoffset: -315.3731994628906; } }

@keyframes drawing_blue_2_infinite {
    0% {
        stroke-dasharray: 130 347.79058837890625;
        stroke-dashoffset: 130; }
    100% {
        stroke-dasharray: 1 347.79058837890625;
        stroke-dashoffset: -347.79058837890625; } }

@media only screen and (max-width: 1200px) {
    .logo {
        width: 320px;
        height: 200px;
    }
}

@media only screen and (max-width: 992px) {
    .logo {
        width: 300px;
        height: 200px;
    }
}

@media only screen and (max-width: 768px) {
    .logo {
        width: 200px;
        height: 120px;
        margin-top: 16px;
    }
}

@media only screen and (max-width: 576px) {
    .logo {
        width: 130px;
        height: 90px;
        margin-top: 14px;
    }
}
