.loading-line {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 20px;
    z-index: 1000;
    overflow: hidden;
}
  
#svg-loading-line {
    width: 100%;
    height: 20px;
}

#path-loading-line{
    stroke-dasharray: 1920;
    stroke-dashoffset: 1920;
}

@media only screen and (max-width: 768px) {
    .loading-line {
        height: 16px;
    }
}

@media only screen and (max-width: 576px) {
    .loading-line {
        height: 14px;
    }
}