@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 200;
  src: url("/fonts/Raleway/Raleway-ExtraLight.eot?#iefix") format("embedded-opentype"), url("/fonts/Raleway/Raleway-ExtraLight.woff") format("woff"), url("/fonts/Raleway/Raleway-ExtraLight.ttf") format("truetype"); }

@font-face {
  font-family: "Shrewdy";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Shrewdy/Shrewdy.eot?#iefix") format("embedded-opentype"), url("/fonts/Shrewdy/Shrewdy.woff") format("woff"), url("/fonts/Shrewdy/Shrewdy.ttf") format("truetype"); }


* {
    margin: 0;
    padding: 0;
}

body {
    position: relative;
    -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
            box-sizing: border-box;
    padding-top: 20px;
    font: normal 200 15px / 1.2  "Raleway", sans-serif;
    background-color: #1f293c;
    color: #ffffff;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 768px) {
  body {
    padding-top: 16px;
  }
}
@media only screen and (max-width: 576px) {
  body {
    padding-top: 14px;
  }
}